import React, { Component } from "react";
import Navbar from "../../Components/Navbar";
import "./HomePage.css";
import ReactPlayer from "react-player";

export default class HomePage extends Component {
  render() {
    return (
      <div>
        <div class="jumbotron jumbotron-fluid image" id="homeBackground">
          <div class="container-fluid">
            <Navbar />
            <h1 class="display-4 header">
              Hello. I'm <br /> Adrienne
              <br /> Scasny.
            </h1>
            <div class="arrow bounce">
              <a class="fas fa-chevron-down fa-2x" id="bounceArrow1" />
              <a class="fas fa-chevron-down" id="bounceArrow2" />
            </div>
          </div>
        </div>
        <div class="jumbotron jumbotron-fluid">
          <div className="player-wrapper">
            <ReactPlayer
              className="react-player"
              url="https://vimeo.com/330638688"
              width="100%"
              height="100%"
            />
          </div>
        </div>
      </div>
    );
  }
}
